"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, RefreshCw, Eye, CheckCircle2, XCircle, MousePointerClick } from "lucide-react"
import { useCampaigns } from "@/hooks/use-campaigns"
import { Badge } from "@/components/ui/badge"
import { TimelineModal } from "./timeline-modal"
import { CreateCampaignModal, type CampaignFormData } from "./create-campaign-modal-v2"

export function ObservabilityDashboard() {
  const { campaigns } = useCampaigns()
  const [createModalOpen, setCreateModalOpen] = useState(false)
  const [timelineOpen, setTimelineOpen] = useState(false)
  const [selectedEmail, setSelectedEmail] = useState<any>(null)
  const [selectedCampaignId, setSelectedCampaignId] = useState<string | null>(null)

  const handleViewCampaignDetails = (campaignId: string) => {
    setSelectedCampaignId(campaignId)
    // Could open a detail modal or navigate
  }

  const handleViewEmailDetails = (email: any, campaignId: string) => {
    setSelectedEmail({ ...email, campaignId })
    setTimelineOpen(true)
  }

  const getStatusBadge = (status: string) => {
    const statusConfig = {
      active: { label: "Active", className: "bg-green-100 text-green-800 border-green-200" },
      completed: { label: "Completed", className: "bg-blue-100 text-blue-800 border-blue-200" },
      draft: { label: "Draft", className: "bg-gray-100 text-gray-800 border-gray-200" },
      paused: { label: "Paused", className: "bg-yellow-100 text-yellow-800 border-yellow-200" },
    }
    const config = statusConfig[status as keyof typeof statusConfig] || statusConfig.draft
    return (
      <Badge className={`${config.className} border text-xs font-medium`}>{config.label}</Badge>
    )
  }

  return (
    <div className="p-4 md:p-8 space-y-6 bg-background min-h-screen">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground">Campaign Observability</h1>
          <p className="text-muted-foreground mt-1">
            Monitor all launched campaigns and their real-time status
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Button
            onClick={() => setCreateModalOpen(true)}
            className="bg-primary hover:bg-primary/90 text-primary-foreground gap-2"
          >
            <Plus className="w-4 h-4" />
            New Campaign
          </Button>
          <Button variant="outline" className="border-border bg-transparent gap-2">
            <RefreshCw className="w-4 h-4" />
            Refresh
          </Button>
        </div>
      </div>

      {/* Campaigns Table */}
      <Card className="bg-card border border-border shadow-sm">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-border bg-secondary">
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Campaign Name
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Target Group
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Total Sent
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Delivered
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Bounced
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Clicked
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Status
                </th>
                <th className="text-left py-4 px-6 text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody>
              {campaigns.map((campaign) => {
                const bouncedCount = campaign.emails?.filter((e: any) => e.status === "bounced").length || 0
                const clickedCount = campaign.clicked || 0
                const clickRate = campaign.totalSent > 0 ? ((clickedCount / campaign.totalSent) * 100).toFixed(1) : "0.0"
                
                return (
                  <tr
                    key={campaign.id}
                    className="border-b border-border hover:bg-secondary/50 transition-colors cursor-pointer"
                    onClick={() => handleViewCampaignDetails(campaign.id)}
                  >
                    <td className="py-4 px-6">
                      <div className="font-medium text-foreground">{campaign.name}</div>
                    </td>
                    <td className="py-4 px-6 text-sm text-muted-foreground">
                      {campaign.status === "active" ? "Marketing Team" : "All Employees"}
                    </td>
                    <td className="py-4 px-6">
                      <div className="text-sm font-semibold text-foreground">
                        {campaign.totalSent || 0}
                      </div>
                    </td>
                    <td className="py-4 px-6">
                      <div className="flex items-center gap-2">
                        <CheckCircle2 className="w-4 h-4 text-green-600" />
                        <span className="text-sm text-foreground">{campaign.delivered || 0}</span>
                      </div>
                    </td>
                    <td className="py-4 px-6">
                      <div className="flex items-center gap-2">
                        <XCircle className="w-4 h-4 text-red-600" />
                        <span className="text-sm text-foreground">{bouncedCount}</span>
                      </div>
                    </td>
                    <td className="py-4 px-6">
                      <div className="flex items-center gap-2">
                        <MousePointerClick className="w-4 h-4 text-primary" />
                        <div>
                          <span className="text-sm font-semibold text-foreground">{clickedCount}</span>
                          <span className="text-xs text-muted-foreground ml-1">({clickRate}%)</span>
                        </div>
                      </div>
                    </td>
                    <td className="py-4 px-6">{getStatusBadge(campaign.status)}</td>
                    <td className="py-4 px-6">
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={(e) => {
                          e.stopPropagation()
                          if (campaign.emails && campaign.emails.length > 0) {
                            handleViewEmailDetails(campaign.emails[0], campaign.id)
                          }
                        }}
                        className="text-primary hover:text-primary hover:bg-primary/10"
                      >
                        <Eye className="w-4 h-4 mr-1" />
                        View
                      </Button>
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </div>

        {campaigns.length === 0 && (
          <div className="p-12 text-center">
            <p className="text-muted-foreground">No campaigns yet. Create your first campaign to get started.</p>
          </div>
        )}
      </Card>

      {/* Campaign Detail View - Show when campaign is selected */}
      {selectedCampaignId && (
        <Card className="bg-card border border-border shadow-sm">
          <div className="p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-bold text-foreground">
                {campaigns.find((c) => c.id === selectedCampaignId)?.name || "Campaign Details"}
              </h2>
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setSelectedCampaignId(null)}
                className="text-muted-foreground"
              >
                Close
              </Button>
            </div>

            <div className="space-y-4">
              <h3 className="text-sm font-semibold text-foreground uppercase tracking-wider">
                Individual Email Status
              </h3>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b border-border bg-secondary">
                      <th className="text-left py-3 px-4 text-xs font-semibold text-muted-foreground">
                        Status
                      </th>
                      <th className="text-left py-3 px-4 text-xs font-semibold text-muted-foreground">
                        Recipient
                      </th>
                      <th className="text-left py-3 px-4 text-xs font-semibold text-muted-foreground">
                        Subject
                      </th>
                      <th className="text-left py-3 px-4 text-xs font-semibold text-muted-foreground">
                        Sent At
                      </th>
                      <th className="text-left py-3 px-4 text-xs font-semibold text-muted-foreground">
                        Actions
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {campaigns
                      .find((c) => c.id === selectedCampaignId)
                      ?.emails?.map((email: any) => (
                        <tr
                          key={email.id}
                          className="border-b border-border hover:bg-secondary/50 transition-colors"
                        >
                          <td className="py-3 px-4">
                            {email.status === "delivered" ? (
                              <Badge className="bg-green-100 text-green-800 border-green-200 text-xs">
                                Delivered
                              </Badge>
                            ) : email.status === "bounced" ? (
                              <Badge className="bg-red-100 text-red-800 border-red-200 text-xs">
                                Bounced
                              </Badge>
                            ) : email.status === "clicked" ? (
                              <Badge className="bg-primary/10 text-primary border-primary/20 text-xs">
                                Clicked
                              </Badge>
                            ) : (
                              <Badge className="bg-gray-100 text-gray-800 border-gray-200 text-xs">
                                {email.status}
                              </Badge>
                            )}
                          </td>
                          <td className="py-3 px-4 text-sm text-foreground">{email.recipient}</td>
                          <td className="py-3 px-4 text-sm text-foreground">{email.subject}</td>
                          <td className="py-3 px-4 text-sm text-muted-foreground">{email.sentAt}</td>
                          <td className="py-3 px-4">
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => handleViewEmailDetails(email, selectedCampaignId)}
                              className="text-primary hover:text-primary hover:bg-primary/10"
                            >
                              <Eye className="w-4 h-4" />
                            </Button>
                          </td>
                        </tr>
                      ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </Card>
      )}

      {/* Modals */}
      <CreateCampaignModal
        open={createModalOpen}
        onOpenChange={setCreateModalOpen}
        onSubmit={(data) => {
          console.log("Campaign created:", data)
          setCreateModalOpen(false)
        }}
      />

      <TimelineModal
        open={timelineOpen}
        onOpenChange={setTimelineOpen}
        email={selectedEmail}
      />
    </div>
  )
}

