"use client"

import { useState, useEffect } from "react"
import { Database, FileText, Mail, Send, CheckCircle2 } from "lucide-react"

interface Animation1Props {
  onComplete: () => void
}

// Animation 1: AI Email Generation
export function AIGenerationAnimation({ onComplete }: Animation1Props) {
  const [phase, setPhase] = useState<"search" | "generate" | "ready">("search")
  const [generatedText, setGeneratedText] = useState("")
  const [searchProgress, setSearchProgress] = useState(0)

  useEffect(() => {
    // Phase 1: Context Search
    const searchInterval = setInterval(() => {
      setSearchProgress((prev) => {
        if (prev >= 100) {
          clearInterval(searchInterval)
          setTimeout(() => setPhase("generate"), 500)
          return 100
        }
        return prev + 10
      })
    }, 200)

    return () => clearInterval(searchInterval)
  }, [])

  useEffect(() => {
    if (phase === "generate") {
      // Phase 2: Text Generation Stream
      const sampleText = "Hi Sarah,\n\nYour Q1 Security Training credentials have been processed and are now ready to use. Please click the link below to access your personalized dashboard and complete the required modules.\n\nAccess Your Dashboard: https://security-training.company.com/verify?token=9a7k3m2p\n\nRequired Modules:\n- Phishing Recognition (45 min)\n- Password Security Best Practices (30 min)\n- Data Classification Training (40 min)\n\nImportant: You must complete all modules by January 31, 2025. Your progress will be tracked automatically.\n\nIf you have any questions or experience technical issues, contact our support team at training-support@company.com\n\nBest regards,\nCorporate Security Team"
      
      let index = 0
      const generateInterval = setInterval(() => {
        if (index < sampleText.length) {
          setGeneratedText(sampleText.slice(0, index + 1))
          index += 3
        } else {
          clearInterval(generateInterval)
          setTimeout(() => {
            setPhase("ready")
            setTimeout(() => onComplete(), 1000)
          }, 500)
        }
      }, 30)

      return () => clearInterval(generateInterval)
    }
  }, [phase, onComplete])

  return (
    <div className="flex flex-col items-center justify-center gap-8 py-12 px-6">
      {/* Phase 1: Context Search */}
      {phase === "search" && (
        <div className="w-full max-w-2xl space-y-6">
          <div className="text-center space-y-2">
            <div className="flex items-center justify-center gap-2">
              <Database className="w-5 h-5 text-primary animate-pulse" />
              <p className="text-lg font-semibold text-foreground">Analyzing context for targets...</p>
            </div>
            <p className="text-sm text-muted-foreground">Searching Supabase for relevant employee data</p>
          </div>

          <div className="relative">
            {/* Database Icon */}
            <div className="flex items-center justify-center mb-8">
              <div className="relative">
                <div className="w-24 h-24 border-4 border-primary/20 rounded-lg flex items-center justify-center">
                  <Database className="w-12 h-12 text-primary animate-pulse" />
                </div>
                {/* Animated search waves */}
                <div className="absolute inset-0 border-4 border-primary rounded-lg animate-ping opacity-20" />
                <div className="absolute inset-0 border-4 border-primary rounded-lg animate-ping opacity-10" style={{ animationDelay: "0.5s" }} />
              </div>
            </div>

            {/* Progress Bar */}
            <div className="w-full bg-secondary rounded-full h-3 border border-border overflow-hidden">
              <div
                className="bg-gradient-to-r from-primary to-accent h-full transition-all duration-300 rounded-full"
                style={{ width: `${searchProgress}%` }}
              />
            </div>
            <p className="text-xs text-muted-foreground text-center mt-2">{searchProgress}% complete</p>
          </div>
        </div>
      )}

      {/* Phase 2: Generation Stream */}
      {phase === "generate" && (
        <div className="w-full max-w-2xl space-y-6">
          <div className="text-center space-y-2">
            <div className="flex items-center justify-center gap-2">
              <FileText className="w-5 h-5 text-primary animate-pulse" />
              <p className="text-lg font-semibold text-foreground">Generating tailored message...</p>
            </div>
            <p className="text-sm text-muted-foreground">LLM crafting personalized phishing email</p>
          </div>

          {/* Document with streaming text */}
          <div className="bg-secondary border-2 border-primary/30 rounded-lg p-6 min-h-[300px] relative overflow-hidden">
            <div className="absolute top-4 right-4">
              <div className="flex gap-1">
                <div className="w-2 h-2 bg-red-500 rounded-full" />
                <div className="w-2 h-2 bg-yellow-500 rounded-full" />
                <div className="w-2 h-2 bg-green-500 rounded-full" />
              </div>
            </div>
            <div className="mt-6 space-y-2 font-mono text-sm text-foreground whitespace-pre-wrap">
              {generatedText}
              <span className="inline-block w-2 h-4 bg-primary animate-pulse ml-1" />
            </div>
          </div>
        </div>
      )}

      {/* Phase 3: Ready - Email in Envelope */}
      {phase === "ready" && (
        <div className="w-full max-w-2xl space-y-6 animate-scale-in">
          <div className="text-center space-y-2">
            <CheckCircle2 className="w-8 h-8 text-green-600 mx-auto" />
            <p className="text-lg font-semibold text-foreground">Email Generated Successfully!</p>
          </div>

          {/* Envelope with email sliding in */}
          <div className="flex items-center justify-center">
            <div className="relative">
              {/* Envelope */}
              <div className="w-32 h-24 bg-primary/10 border-2 border-primary rounded-lg relative overflow-hidden">
                {/* Envelope flap */}
                <div className="absolute top-0 left-0 right-0 h-12 bg-primary/20 transform -skew-y-6 origin-top animate-slide-down" />
                {/* Email document sliding in */}
                <div className="absolute bottom-2 left-2 right-2 h-16 bg-card border border-border rounded shadow-lg animate-slide-up">
                  <div className="p-2 text-xs text-foreground truncate">Email ready to send...</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

interface Animation2Props {
  totalEmails: number
  onComplete: () => void
}

// Animation 2: Mass Sending
export function MassSendingAnimation({ totalEmails, onComplete }: Animation2Props) {
  const [sentCount, setSentCount] = useState(0)
  const [envelopeClosed, setEnvelopeClosed] = useState(false)
  const [envelopesFlying, setEnvelopesFlying] = useState(false)

  useEffect(() => {
    // Close envelope first
    setTimeout(() => {
      setEnvelopeClosed(true)
      setTimeout(() => setEnvelopesFlying(true), 500)
    }, 500)

    // Simulate sending progress
    const sendInterval = setInterval(() => {
      setSentCount((prev) => {
        if (prev >= totalEmails) {
          clearInterval(sendInterval)
          setTimeout(() => onComplete(), 1000)
          return totalEmails
        }
        // Increment faster at start, slower near end
        const increment = prev < totalEmails * 0.8 ? Math.ceil(totalEmails / 20) : 1
        return Math.min(prev + increment, totalEmails)
      })
    }, 100)

    return () => clearInterval(sendInterval)
  }, [totalEmails, onComplete])

  return (
    <div className="flex flex-col items-center justify-center gap-8 py-12 px-6">
      <div className="text-center space-y-2">
        <div className="flex items-center justify-center gap-2">
          <Send className="w-5 h-5 text-primary animate-pulse" />
          <p className="text-lg font-semibold text-foreground">
            {sentCount < totalEmails ? "Mass sending via Resend..." : "Campaign Live. Awaiting Webhook Events..."}
          </p>
        </div>
        <p className="text-sm text-muted-foreground">
          Sending {sentCount} / {totalEmails} emails
        </p>
      </div>

      <div className="w-full max-w-3xl relative">
        {/* Source Envelope */}
        <div className="absolute left-1/4 top-1/2 -translate-x-1/2 -translate-y-1/2">
          <div className="relative">
            {!envelopeClosed ? (
              <div className="w-24 h-16 bg-primary/10 border-2 border-primary rounded-lg relative">
                <div className="absolute top-0 left-0 right-0 h-8 bg-primary/20 transform -skew-y-6 origin-top" />
                <div className="absolute bottom-2 left-2 right-2 h-8 bg-card border border-border rounded" />
              </div>
            ) : (
              <div className="w-24 h-16 bg-primary/10 border-2 border-primary rounded-lg relative">
                <div className="absolute top-0 left-0 right-0 h-8 bg-primary/20 transform skew-y-0 origin-top transition-all duration-500" />
              </div>
            )}
          </div>
        </div>

        {/* Flying Envelopes */}
        {envelopesFlying && (
          <div className="absolute left-1/2 top-1/2 -translate-y-1/2 w-1/2">
            {Array.from({ length: Math.min(10, Math.ceil(sentCount / (totalEmails / 10))) }).map((_, i) => (
              <div
                key={i}
                className="absolute animate-fly-out"
                style={{
                  left: `${(i % 5) * 20}%`,
                  top: `${Math.floor(i / 5) * 30}%`,
                  animationDelay: `${i * 0.1}s`,
                }}
              >
                <Mail className="w-6 h-6 text-primary" />
              </div>
            ))}
          </div>
        )}

        {/* Destination Mailboxes/Cloud */}
        <div className="absolute right-1/4 top-1/2 -translate-y-1/2">
          <div className="flex flex-col gap-4 items-center">
            {Array.from({ length: 3 }).map((_, i) => (
              <div
                key={i}
                className={`w-12 h-12 border-2 border-primary rounded-lg flex items-center justify-center transition-all ${
                  sentCount > (i + 1) * (totalEmails / 3) ? "bg-green-500/20 border-green-500" : "bg-secondary border-border"
                }`}
              >
                <Mail className={`w-6 h-6 ${sentCount > (i + 1) * (totalEmails / 3) ? "text-green-600" : "text-muted-foreground"}`} />
              </div>
            ))}
          </div>
        </div>

        {/* Progress Counter */}
        <div className="absolute bottom-0 left-0 right-0 text-center">
          <div className="text-4xl font-bold text-primary mb-2">
            {sentCount} / {totalEmails}
          </div>
          <div className="w-full bg-secondary rounded-full h-2 border border-border overflow-hidden">
            <div
              className="bg-gradient-to-r from-primary to-accent h-full transition-all duration-100 rounded-full"
              style={{ width: `${(sentCount / totalEmails) * 100}%` }}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

